<template>
    <div>
        <div class="title">数据分析</div>
        <div class="tips">注意：仅支持已上传的简历</div>
        <div class="box">
            <div class="sex" id="sex"></div>
            <div class="age" id="age"></div>
            <div class="line" id="line"></div>
            <div class="edu" id="edu"></div>
        </div>
    </div>
</template>

<script>
import { reqDataVisual } from '@/api/index'
export default {
    name: 'dataAnalyze',
    data() {
        return {
            man: 0,
            women: 0,
            unKonwn: 0,
            age0: 0,
            age1: 0,
            age2: 0,
            age3: 0,
            age4: 0,
            age5: 0,
            age6: 0,
            uploadCv: 0,
            collectCv: 0,
        }
    },
    methods: {
        async getData() {
            const res = await reqDataVisual(localStorage.getItem('id'))
            let data = res.data
            this.man = data.sexNums.man
            this.women = data.sexNums.women
            this.unKnown = data.sexNums.unKnown
            this.age0 = data.ageNums.age0
            this.age1 = data.ageNums.age1
            this.age2 = data.ageNums.age2
            this.age3 = data.ageNums.age3
            this.age4 = data.ageNums.age4
            this.age5 = data.ageNums.age5
            this.age6 = data.ageNums.age6
            this.uploadCv = data.uploadCv
            this.collectCv = data.collectCv
            this.edu0=data.educationNums.edu0
            this.edu1=data.educationNums.edu1
            this.edu2=data.educationNums.edu2
            this.edu3=data.educationNums.edu3
            this.edu4=data.educationNums.edu4
            this.edu5=data.educationNums.edu5
            this.edu6=data.educationNums.edu6
            
            this.sexImg(),
                this.ageImg(),
                this.eduImg(),
                this.lineImg()
        },
        sexImg() {
            var chartDom = document.getElementById('sex');
            var myChart = this.$echarts.init(chartDom);
            var option;
            option = {
                title: {
                    text: '性别',
                    left: 'center',
                    top: '0'
                },
                tooltip: {
                    trigger: 'item',

                },
                dataset: [
                    {
                        source: [
                            ['性别', 'Sales', 'Year'],
                            ['男', this.man, 2011],
                            ['未知', this.unKnown, 2011],
                            ['女', this.women, 2011]
                        ]
                    },
                    {
                        transform: {
                            type: 'filter',
                            config: { dimension: 'Year', value: 2011 }
                        }
                    },
                ],
                series: [
                    {
                        type: 'pie',
                        radius: 150,
                        center: ['100%', '100%'],
                        datasetIndex: 1
                    },
                ],
                // Optional. Only for responsive layout:
                media: [
                    {
                        query: { minAspectRatio: 1 },
                        option: {
                            series: [
                                { center: ['50%', '50%'] },
                            ]
                        }
                    },
                ]
            };

            option && myChart.setOption(option);
        },
        ageImg() {
            var chartDom = document.getElementById('age');
            var myChart = this.$echarts.init(chartDom);
            var option;
            myChart.resize({
                width: 600,
                height: 500
            })
            option = {
                title: {
                    text: '年龄',
                    left: 'center',
                    top: '0'
                },
                tooltip: {
                    trigger: 'item',

                },

                xAxis: {
                    type: 'category',
                    data: ['18-22', '23-27', '28-32', '33-37', '38-42', '43-47', '48+']
                },
                yAxis: {
                    type: 'value'
                },
                series: [
                    {
                        data: [this.age0, this.age1, this.age2, this.age3, this.age4, this.age5, this.age6],
                        type: 'bar'
                    }
                ]
            };

            option && myChart.setOption(option);

        },
        eduImg() {
            var chartDom = document.getElementById('edu');
            var myChart = this.$echarts.init(chartDom);
            var option;

            option = {
                title: {
                    text: '学历',
                    left: 'center',
                    top: '0'
                },
                tooltip: {
                    trigger: 'item',
                    formatter: ' {c} ({d}%)'
                },
                series: [
                    {
                        name: 'Area Mode',
                        type: 'pie',
                        radius: [40, 200],
                        center: ['50%', '50%'],
                        roseType: 'area',
                        itemStyle: {
                            borderRadius: 5
                        },
                        data: [
                            { value: this.edu2, name: '大专' },
                            { value: this.edu5, name: '博士' },
                            { value: this.edu4, name: '硕士' },
                            { value: this.edu6, name: '博士后' },
                            { value: this.edu3, name: '本科' },
                            { value: this.edu1, name: '大专以下' },
                        ]
                    }
                ]
            };

            option && myChart.setOption(option);

        },
        lineImg() {

            var chartDom = document.getElementById('line');
            var myChart = this.$echarts.init(chartDom);
            var option;

            option = {
                color:['#c0ebd7', '#faff72'],
                title: {
                    text: '简历情况',
                    left: 'center',
                    top: '30px'
                },
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'shadow'
                    }
                },
                legend: {},
                grid: {
                    left: '3%',
                    right: '4%',
                    bottom: '3%',
                    containLabel: true
                },
                xAxis: {
                    type: 'value',
                    boundaryGap: [0, 0.01]
                },
                yAxis: {
                    type: 'category',
                    data: ['简历情况']
                },
                series: [
                    {
                        name: '已上传简历',
                        type: 'bar',
                        data: [this.uploadCv]
                    },
                    {
                        name: '已收藏简历',
                        type: 'bar',
                        data: [this.collectCv]
                    }
                ]
            };

            option && myChart.setOption(option);

        }
    },
    mounted() {
        this.getData()
    }
}
</script>

<style lang="scss" scoped>
.title {
    width: 200px;
    height: 80px;
    line-height: 80px;
    text-align: center;
    margin: 0 auto;
    font-size: 30px;
    font-weight: 700;
}

.tips {
    margin: 0 auto;
    width: 200px;
    text-align: center;
    transform: translateY(-10px);
}

.box {
    height: 1200px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    margin-top: 100px;
    margin-bottom: 100px;

    .sex {
        height: 600px;
        width: 50%;
    }

    .age {
        height: 600px;
        width: 50%;
    }

    .edu {
        height: 600px;
        width: 50%;
    }

    .line {
        height: 600px;
        width: 50%;
    }
}
</style>